import React, { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { Row, Col, Divider, Card } from 'antd';
import { pageLoadedLogin } from '../../../../application/actions/ui';
import FormLaporanKirimSaldoTahun from '../../../component/reportComponent/kirim_saldo_tahun/form';
import TableLaporanKirimSaldoTahun from '../../../component/reportComponent/kirim_saldo_tahun/tabel';
import Btnprint from '../../../component/reportComponent/kirim_saldo_tahun/btnprint';

const LaporanKirimSaldoTahun = () => {
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(pageLoadedLogin);
    document.title = 'Laporan Kirim dan Saldo per Tahun';
  }, [dispatch]);

  return (
    <Card title="Laporan Kirim dan Saldo per Tahun" bordered={false}>
      <Row>
        <Col span={24}>
          <FormLaporanKirimSaldoTahun />
        </Col>
        <Col
          span={24}
          // style={{ marginTop: 10, display: datalapR.length === 0 ? 'none' : '' }}
        >
          <Divider orientation="left">Tabel Laporan</Divider>
          <TableLaporanKirimSaldoTahun />
        </Col>
        <Col
          span={24}
          // style={{ marginTop: 10, display: datalapR.length === 0 ? 'none' : '' }}
        >
          <Btnprint />
        </Col>
      </Row>
    </Card>
  );
};

export default LaporanKirimSaldoTahun;
